<template>
    <div class="recommend">
        <scroll class="recommend-content" :data="discList">
            <div>
                <!-- 轮播图 -->
                <div v-if="banner.length" class="slider-wrapper" ref="sliderWrapper">
                    <slider>
                        <div v-for="(item, index) in banner" :key="item.index">
                            <a :href="item.jumpurl">
                                <img :src="item.pic" class="needsclick" />
                            </a>
                        </div>
                    </slider>
                </div>
                <!-- 热门歌单推荐 -->
                <div class="recommend-list">
                    <h1 class="list-title">热门歌单推荐</h1>
                    <ul>
                        <li
                            @click="selectItem(item)"
                            v-for="(item, index) in discList"
                            :key="index"
                            class="item"
                        >
                            <div class="icon">
                                <img width="60" v-lazy="item.imgurl" />
                            </div>
                            <div class="text">
                                <h2 class="name" v-html="item.author"></h2>
                                <p class="desc" v-html="item.dissname"></p>
                            </div>
                        </li>
                    </ul>
                </div>
                <!-- 加载区 -->
                <div class="loading-container" v-show="!discList.length">
                    <loading></loading>
                </div>
            </div>
        </scroll>
        <router-view></router-view>
    </div>
</template>

<script>
import { getMusicData } from "api/recommend";
import { ERR_OK } from "api/config";
import Slider from "base/slider/slider";
import Scroll from "base/scroll/scroll";
import Loading from "base/loading/loading";
import { mapMutations, mapGetters } from "vuex";
import { playlistMixin } from "common/js/mixin";

export default {
    mixins: [playlistMixin],
    components: {
        Slider,
        Scroll,
        Loading
    },
    created() {
        this._getData();
    },
    methods: {
        _getData() {
            if (!this.banner.length && !this.discList.length) {
                getMusicData().then(res => {
                    if (res.code === 0) {
                        this.setMusicData(res.data);
                    }
                });
            }
        },
        selectItem(item) {
            this.$router.push({
                path: `/recommend/${item.dissid}`
            });
            this.setDisc(item);
        },
        ...mapMutations({
            setMusicData: "SET_MUSICDATA",
            setDisc: 'SET_DISC'
        })
    },
    computed: {
        ...mapGetters(["banner", "discList"])
    }
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/variable';

.recommend {
    position: fixed;
    width: 100%;
    top: 88px;
    bottom: 0;

    .recommend-content {
        height: 100%;
        overflow: hidden;

        .slider-wrapper {
            position: relative;
            width: 100%;
            overflow: hidden;
        }

        .recommend-list {
            .list-title {
                height: 65px;
                line-height: 65px;
                text-align: center;
                font-size: $font-size-medium;
                color: $color-theme;
            }

            .item {
                display: flex;
                box-sizing: border-box;
                align-items: center;
                padding: 0 20px 20px 20px;

                .icon {
                    flex: 0 0 60px;
                    width: 60px;
                    padding-right: 20px;
                }

                .text {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    flex: 1;
                    line-height: 20px;
                    overflow: hidden;
                    font-size: $font-size-medium;

                    .name {
                        margin-bottom: 10px;
                        color: $color-text;
                    }

                    .desc {
                        color: $color-text-d;
                    }
                }
            }
        }

        .loading-container {
            position: absolute;
            width: 100%;
            top: 50%;
            transform: translateY(-50%);
        }
    }
}
</style>


